body {
  margin: 0;
  padding: 0;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

input {
  box-sizing: border-box;
  padding: 8px;
  border: 1px solid #eee;
}


.app {
  text-align: center;
}

.app-header {
  background-color: rgba(188, 189, 202, 0.536);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 1vmin);
  color: black;
  padding: 40px;
}

.app-header span {
  margin: 20px;
  font-size: calc(26px + 2vmin);
  font-weight: 800;
}

.createtask__button {
  background-color: rgba(35, 35, 239, 0.913);
  color: white;
  padding: 16px;
  padding-left: 50px;
  padding-right: 50px;
  border-radius: 20px;
  -webkit-transition: all 0.2s;
  transition: all 0.2s; 
  cursor: pointer;
}

.createtask__button:hover {
  opacity: 0.6;
}
.modal {
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0;
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    opacity: 0;
    pointer-events: none;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.modal.active {
    opacity: 1;
    pointer-events: all;
}

.modal__content {
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    width: 40vw;
    min-width: 270px;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    -webkit-transition: 0.2s all;
    transition: 0.2s all;
}

.modal__content.active {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.new_task {
    margin: 10px;
}

.new_task__entering p {
    text-align: left;
    margin-left: 1vw;
    margin-bottom: 5px;
}

.new_task__entering textarea {
    width: 37vw;
    min-width: 250px;
    max-height: 50px;
    height: 50px;
    font-size: 20px;
}

.new_task__entering input {
    width: 37vw;
    min-width: 250px;
    max-height: 50px;
    height: 50px;
    font-size: 20px;
}

.new_task__buttons {
    margin-top: 50px;
}

.new_task__buttons :first-child {
    background-color: rgba(35, 35, 239, 0.913);
    color: white;
    padding: 1.5vh;
    padding-left: 2vw;
    padding-right: 2vw;
    border-radius: 20px;
    -webkit-transition: all 0.2s;
    transition: all 0.2s; 
    margin-right: 10px;
    cursor: pointer;
}

.new_task__buttons :last-child {
    background-color: rgba(35, 35, 239, 0.913);
    color: white;
    padding: 1.5vh;
    padding-left: 2vw;
    padding-right: 2vw;
    border-radius: 20px;
    -webkit-transition: all 0.2s;
    transition: all 0.2s; 
    cursor: pointer;
}

.new_task__buttons a:hover {
    opacity: 0.6;
}


