    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.4);
    position: fixed;
    top: 0px;  
    left: 0;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    opacity: 0;
    pointer-events: none;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.modalok.active {
    opacity: 1;
    pointer-events: all;
}

.modalok__content {
    padding: 20px;
    border-radius: 12px;
    background-color: white;
    width: 20vw;
    min-width: 200px;
    -webkit-transform: scale(0.5);
            transform: scale(0.5);
    -webkit-transition: 0.2s all;
    transition: 0.2s all;
}

.modalok__content.active {
    -webkit-transform: scale(1);
            transform: scale(1);
}

.new_modal {
    margin: 10px;
    opacity: inherit;
}

.new_modal__entering p {
    margin-bottom: 5px;
}

.new_modal__buttons {
    margin-top: 50px;
    margin-bottom: 20px;
}

.new_modal__buttons :first-child {
    background-color: rgba(35, 35, 239, 0.913);
    color: white;
    padding: 1.3vh;
    padding-left: 4vw;
    padding-right: 4vw;
    border-radius: 20px;
    -webkit-transition: all 0.2s;
    transition: all 0.2s; 
    cursor: pointer;
}

.new_modal__buttons:hover {
    opacity: 0.6;
}

.tasks {
  list-style-type: none;
  display: -webkit-flex;
  display: flex;
  margin: 0;
  padding: 0;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
  
.task__notmissed {
  display: -webkit-flex;
  display: flex;
  background-color: rgb(227, 217, 217);
  /* background-color: rgb(227, 217, 217); */
  width: 20%;
  min-width: 300px;
  height: 30vh;
  min-height: 250px;
  margin: calc(2.3vw - 0.05vw);
  border-width: 0.05vw;
  border-color: #eee;
  border-style: solid;
  border-radius: 4px;
  overflow: auto;
  -webkit-transform: translateY(-150%);
          transform: translateY(-150%);
  -webkit-animation: ani 1s forwards;
          animation: ani 1s forwards;
}

.task__missed {
  display: -webkit-flex;
  display: flex;
  background-color: rgb(237, 174, 174);
  width: 20%;
  min-width: 300px;
  height: 30vh;
  min-height: 250px;
  margin: calc(2.3vw - 0.05vw);
  border-width: 0.05vw;
  border-color: #eee;
  border-style: solid;
  border-radius: 4px;
  overflow: auto;
  -webkit-transform: translateY(-150%);
          transform: translateY(-150%);
  -webkit-animation: ani 1s forwards;
          animation: ani 1s forwards;
}

.task__content {
  margin-bottom: 2px;
  text-align: left;
  margin-left: 15px;
  word-wrap: break-word;
  font-size: calc(10px + 1vmin);
}

.task__content h4 {
  margin-top: 1.5vh;
  margin-bottom: 0.5vh;
}

.task__content p {
  margin: 0;
}

.task__buttons {
  margin-left: auto;
  margin-right: 0;
  margin-top: 1.2vw;
  margin-right: 1.2vw;
}

.image {
  width: 2vw;
  min-width: 20px;
  -webkit-transition: all 0.1s;
  transition: all 0.1s; 
}

.image:hover {
  opacity: 0.6;
}

@-webkit-keyframes ani {
  20% {-webkit-transform: translateY(-150%);transform: translateY(-150%);}
  100% {-webkit-transform: translateY(0);transform: translateY(0);}
}

@keyframes ani {
  20% {-webkit-transform: translateY(-150%);transform: translateY(-150%);}
  100% {-webkit-transform: translateY(0);transform: translateY(0);}
}
